.projects {
	margin: 30px 0px;
}

.projects-content {
	background: rgb(76, 84, 119);
	color: white;
	padding: 50px;
	box-sizing: border-box;
	margin-bottom: 30px;
}

.projects-description {
	display: flex;
	align-items: center;
	height: 100%;
	width: 40%;
}

.projects-description h3 {
	color: white;
}

.projects-description p {
	color: gray;
}

.projects-description button {
	background: none;
	border: none;
	color: white;
	font-size: 20px;
	margin: 0px;
	padding: 0px;
}

.projects-image {
	background: orange;
	width: 800px;
	height: 550px;
	float: right;
	margin-right: -130px;
	margin-top: calc(-30px - 350px);
}

.projects-nav button {
	background: none;
	border: none;
	font-size: 20px;
	color: gray;
}
